export default {
	data() {
		return {
			id:''
		}
	},
	onLoad(e) {
		this.id = e.id
	},
	methods: {
		// 编辑企业 status=1 是认证完善， status = 0 是重新编辑
		edit1(){
			uni.navigateTo({
				url:"../../pagesTwo/EditEnterpriseIndex/EditEnterpriseIndex?status=0&id="+this.id
			})
		},
		edit2(){
			uni.navigateTo({
				url:"../../pagesTwo/MyBusinessProduct/MyBusinessProduct?id="+this.id
			})
		}
	}
}
